<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process289"></a>

<p><br></p>
<p><span class="rvts17">伊凡·本美兹（</span><span class="rvts50">Ifan Ben-Mezd</span><span class="rvts17">）：</span></p>
<p class="rvps2"><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1552.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts81">故事背景：</span>
<br><span class="rvts21">伊凡</span><span class="rvts20"></span><span class="rvts21">本美兹曾是卢西安手下圣骑军团的孤狼雇佣兵，然而在一次与黑环势力对抗的战役中，卢西安不择手段的误杀了当地的精灵居民，这里面有很多是卢西安的朋友。</span>
<br><span class="rvts21">从此，孤狼被赋予骂名，伊凡恼羞成怒，决定退出孤狼并翻脸要找卢西安算账。卢西安的儿子亚历山大成为伊凡的下一个目标。</span>
<br><span class="rvts20"></span>
<br><span class="rvts81">支线任务：</span>
<br><span class="rvts21">欢乐堡部分：</span>
<br><span class="rvts20">1. </span><span class="rvts21">期初，在伊凡的兜里可以找到一张便条，查看便条内容，并询问伊凡的往事。</span>
<br><span class="rvts20">2. </span><span class="rvts21">在欢乐堡大门处，可以遇到伊凡曾经的同事魔导师波利斯（</span><span class="rvts20">Borris</span><span class="rvts21">），可以与他谈话。</span>
<br><span class="rvts20">3. </span><span class="rvts21">逃出欢乐堡后，找到空洞沼泽里的祖尔斯卡（</span><span class="rvts20">Zaleskar</span><span class="rvts21">），说服祖尔斯卡免于一场打斗，祖尔斯卡会给伊凡一把相当有成色的十字弩。</span>
<br><span class="rvts20">4. </span><span class="rvts21">在支线</span><span class="rvts20"></span><span class="rvts21">转角遇到危险</span><span class="rvts20"></span><span class="rvts21">中成功救出加雷斯，并在阿玛蒂亚避难所询问他关于</span><span class="rvts20"></span><span class="rvts21">孤狼</span><span class="rvts20"></span><span class="rvts21">的一些最新消息。</span>
<br><span class="rvts21">登上复仇女神号部分：</span>
<br><span class="rvts20">5. </span><span class="rvts21">在复仇女神号上，对着被打晕的亚历山大，告诉伊凡</span><span class="rvts20"></span><span class="rvts21">你不会从一个将死之人身上得到任何消息</span><span class="rvts20"></span><span class="rvts21">，这将使伊凡非常高兴。</span>
<br><span class="rvts21">死神海岸部分：</span>
<br><span class="rvts20">6. </span><span class="rvts21">到达死神海岸后，在浮木城刚进门处，一个男孩会给伊凡捎一封信，说巴伦（</span><span class="rvts20">Baran</span><span class="rvts21">）在浮木城的酒吧地下层等候他。找到巴伦后，他会告诉你现任孤狼首领罗斯特（</span><span class="rvts20">Roost</span><span class="rvts21">）在锯木厂想要见他。</span>
<br><span class="rvts20">7. </span><span class="rvts21">在锯木厂见到罗斯特后，罗斯特告诉伊凡亚历山大还活着，并想让伊凡继续效忠于亚历山大。伊凡愤怒的拒绝罗斯特，并杀掉他后，可以从他身上找到</span><span class="rvts20"></span><span class="rvts21">神的命令</span><span class="rvts20"></span><span class="rvts21">信件。</span>
<br><span class="rvts21">无名岛部分：</span>
<br><span class="rvts20">8. </span><span class="rvts21">在无名岛的精灵营地，再次遇到亚历山大，在较为紧张的语言气氛中，伊凡会瞬间刺杀亚历山大，该角色支线就此终结。（如果选择和加雷斯一起干掉亚历山大，伊凡会插不上嘴，可以选择杀死亚历山大后，让伊凡与亚历山大的灵魂谈话，愤怒的伊凡会吸取亚历山大的灵魂，同样伊凡的剧情支线就此完结）</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



